<template>
  <div class="home">
     <br>
    <h1 style="  text-align: center; border-radius: 5px; paddind:5px; margin-left:50px; width:300px;  background-color:#A1C057; color:white;" >Your Profile</h1>
    <br>
    <br>
    <table style="width:50%;  margin-left:300px;" >
  <tr>
    <th>ID:</th>
    <th>{{ user.userId }}</th> 
  </tr>
  <tr>
    <th  >Name:</th>
    <th> {{ user.name }}</th> 
  </tr>
  <tr>
    <th>Email:</th>
    <th>{{ user.email }}</th> 
  </tr>
   <tr>
    <th>Facebook: </th>
    <th>{{ user.url }}</th> 
  </tr>
   <tr>
    <th>Phone:</th>
    <th> {{ user.phone }}</th> 
  </tr>
     </table >
  </div>
  </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'

var md5 = require('md5');

export default {
  name: 'home',
  data () {
    return {
      msg: 'Firebase Authentication tutorial',
      user: {
        photoURL: '',
        userId: '',
        name: '',
        email: '',
        url: '',
        phone: ''
      }
    }
  },
  methods: {
    logout() {
      firebase.auth().signOut().then( () => {
        this.$router.push( { name: 'Login'});
      })
    }
  },
  created() {
    var user = firebase.auth().currentUser;

    if (user) {
      this.user.photoURL = user.photoURL;
      this.user.userId = user.uid;
      this.user.name = user.displayName;
      this.user.email = user.email;

      var uid = md5(user.email);
      console.log(uid);

      db.ref('users/' + uid).once('value').then(
        snapshot => {
          if (snapshot) {
            console.log(snapshot);
            this.user.name = snapshot.val().name;
            this.user.url = snapshot.val().url;
            this.user.phone = snapshot.val().phone;
          }
      });

    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



