<template>
<center>
<br>
    <div class="sign-up"  >
   <h1>Create a new account!</h1>
   <br>
        <div style="width:400px; background-color:#A1C057; padding:30px;" >
        <form @submit.prevent="validateBeforeSubmit" >
         
            <div>
                <input name="name" v-model="user.name"  type="text" placeholder="Full Name" required >
            </div>

            <div>
                <input name="email" v-model="user.email" type="email" placeholder="Email" required >
            </div>

            <div>
                <input name="password" v-model="user.password1" type="password" placeholder="Password" required >
               
            </div>

            <div>
                <input name="confirm password" v-model="user.password2" type="password" placeholder="Confirm Password"
                    required :class="{'input': true, 'is-danger': errors.has('user.password2') }">
                <p v-show="errors.has('confirm password')" style="color: red" >{{ errors.first('confirm password') }}</p>
            </div>

            <div>
                <input name="Birthday" v-model="user.birthdate" type="date" placeholder="Birth day (mm/dd/yyyy)" required  >
            
            </div>

            <div>
                <input name="url" v-model="user.url" type="url" placeholder="Facebook"  required >
            </div>

            <div>
                <input name="phone" v-model="user.phone" type="number" placeholder="Phone" required >
                
            </div>

            <div>
                <button style="margin-bottom: 0px ">Sign Up</button> 
                    <router-link to="/login" style="padding: 5px;  background-color:white; border-radius: 5px; color:black; "> Back to Login</router-link>
              
            </div>
            
        </form>
        </div>
    </div >

</center>
</template>


<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'

var md5 = require('md5');
let usersRef = db.ref('users');
let date = new Date() 

export default {
  name: 'signUp',
  data() {
      return {
          user: {
              name: '',
              email: '',
              password1: '',
              password2: '',
              url: '',
              phone: '',
              birthdate: ''
          },
      }
  },
  methods: {
        validateBeforeSubmit() {
            this.$validator.validateAll().then((result) => {
                if (result) {
                    this.signup();
                }
            });
        },
        signup() {
            firebase.auth().createUserWithEmailAndPassword(this.user.email, this.user.password1).then( 
                user => {
                    alert('Your accound has been created!');

                    /* add user info to database */
                    var newUser = this.addUserInfo();

                    this.$router.push({ name: 'Home'});
                }, 
                error => {
                    var errorCode = error.code;
                    var errorMsg = error.message;
                    if (errorCode == 'auth/weak-password') {
                        alert('The password is too week');
                    } else {
                        alert('Oops.' + errorMsg);
                    }
                }
            );
        },
        addUserInfo() {
            var newUser = {
                name: this.user.name,
                email: this.user.email,
                url: this.user.url,
                phone: this.user.phone,
                birthdate: this.user.birthdate
            }

            var uid = md5(this.user.email);
            console.log(uid);
            db.ref('users/'+ uid).set(newUser);

        }
    }
}
</script>

<style scoped>
    .signUp {
        margin-top: 40px;
       
       
    }
    input {
        margin: 5px 0;
        width: 300px;
        padding: 5px;
    }
    button {
        margin-top: 10px;
        width: 200px;
        padding: 2px;
        border-radius: 5px;
        cursor: pointer;
    }
    span {
        display: block;
        margin-top: 20px;
        font-size: 11px;
    }

</style>





