<template>
   <div>
        <nav class="navbar" style=" background-color: #A1C057; ">
        <ul class="nav nav-pills navbar-right" style="margin-right:5px ;padding:5px; " >
           
            <li role="presentation" ><router-link to="/bookmarker" style="color:black;">Bookmarks</router-link></li>
             <li role="presentation"><router-link to="/home" style="color:black;"> My Profile</router-link></li>
            <li role="presentation"><router-link to="/about" style="color:black;"> About</router-link></li>
            <li role="presentation"><a @click="logout" style="color:black;">Sign Out</a></li>
           
        </ul>
        </nav>
    </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Header",
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push({ name: "Login" });
        });
    }
  }
};
</script>



